.section {
    padding: 20px;
    margin: 20px;
    border: 1px solid #ccc;
    border-radius: 8px;
    background-color: #f9f9f9;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    height: 400px;
  }
  
  h1 {
    font-size: 2em;
    color: #333;
    margin-left: 300px;
  }
  
  p {
    font-size: 1.2em;
    color: #666;
    margin-left: 125px;
  }

  .container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .navbar {
    display: flex;
    justify-content: center;
    background-color: #4CAF50; 
    padding: 15px;
    border-radius: 8px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    margin-bottom: 20px;
    width: 100%;
  }
  
  .nav-link {
    color: white; 
    text-decoration: none;
    padding: 10px 20px;
    border-radius: 5px;
    margin: 0 100px; 
    transition: background-color 0.3s;
    font-size: 2rem;
  }
  
  .nav-link:hover {
    background-color: #45a049; 
  }
  
  .content {
    width: 100%;
    max-width: 800px; 
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 8px;
    background-color: #f9f9f9;
  }
  
  
  